import React, { Component } from 'react';
// import {CardStyle} from './Card.js'
import styled from 'styled-components'
// whenever using a component to go to another link us Link!!
import {Link} from 'react-router-dom'
import {ProductConsumer} from '../context.js'
import PropTypes from 'prop-types'
class Product extends Component {

    handleClick(){
        return(
        console.log("you clicked me"))
    }

    render() {
        const {id, title, img, price, inCart} = this.props.products
        return (
            <ProductWrapper className ='col-9 mx-auto col-md-6 col-lg-3 my-3'>
                <div className ='card'>
                    {/* In order to access productConsumer we need to reference it as value */}
                    <ProductConsumer > 
                        {(value) => (
                        <div className = 'img-container p-5' onClick = { () => value.handleDetail(id)}>

                        <Link to = './Details'>
                            <img src = {img} alt = 'product' className ='card-img-top'></img>
                        </Link>
                        <button className='cart-btn' disabled = {inCart ? true : false} 
                        onClick = {() => {value.addToCart(id); 
                            value.openModel(id);
                        }}>
                            {inCart ? (<p className = 'text-capitalize mb-0' disabled> {''}in cart</p>) : (<i className = "fas fa-cart-plus"></i>)}
                            </button>
                        </div>)}

                    
                
                </ProductConsumer>
                
                    {/* card footer */}
                    <div className = "card-footer d-flex justify-content-between">
                        <p className = 'align-self-center mb-0'>
                            {title}
                        </p>
                        <h5 className = 'text-blue font-italic mb-0'>
                            <span className = 'mr-1'>
                                ${price}
                            </span>
                        </h5>
                        </div> 
                </div>

            </ProductWrapper>
        );
    }
}
// propTypes checks is the json has the corret information in the object
Product.propTypes = {
    product:PropTypes.shape(
        {
            id: PropTypes.number,
            img: PropTypes.string,
            title: PropTypes.string,
            price: PropTypes.number,
            inCart:PropTypes.bool

        }).isRequired
}

const ProductWrapper = styled.div`
.card{
    border-color: transparent;
    transition: all 1s linear;
}
.card-footer{
    background: transparent;
    border-top: transparent;
    transition: all 1s linear;
}
&:hover{
    .card{
        border: 0.04rem solid rgba(0,0,0,0.2);
        box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.02);

    }
    .card-footer{
        background: rgba(247, 247, 247);
    }
}
.img-container{
    position: relative;
    overflow: hidden;
}
.img-container:hover .card-img-top{
    transform:scale(1.2);
}

.card-img-top {
    transition: all 1s linear;
}
.cart-btn{
    position:absolute;
    bottom: 0;
    right: 0;
    padding: 0.2rem 0.4rem;
    background: var(--lightBlue);
    border: none;
    color: var(--mainWhite);
    border-radius: 0.5rem 0 0 0;
    font-size: 1.4rem;
    transform: translate(100%,100%);

}
.img-container:hover .cart-btn{
    transform: translate(0,0);
    transition: all 0.5s linear;
}
.cart-btn:hover {
    color:var(--mainBlue);
    cursor: pointer;
}

`

export default Product;